import React, { Component } from 'react';

class Test extends Component {
	constructor(props) {
		super(props);

		this.state = {
			show: false,
		};

		this.start = undefined;
	}

	componentDidUpdate() {
		const end = new Date().getTime();

		this.refs.result.innerHTML = end - this.start;
	}


	render() {
		return (
			<div className="Test">
				<div className="Test-header">
					<button
						style={{ margin: '1rem' }}
						onClick={() => {
							this.start = new Date().getTime();

							this.setState({ show: ! this.state.show });
						}}
					>
						Button
					</button>
					<span ref="result" style={{ margin: '0 1rem' }}></span>
				</div>
				<div>
					{ this.state.show ?
						this.props.children
						: null
					}
				</div>
			</div>
		);
	}
}

export default Test;



