import React from 'react';

import Button from '../lib/MaterialDesignTheme/ButtonUO/ButtonWave';

import arrayGenerator from '../lib/Common/Util/arrayGenerator';

const El = ({count}) => {
	return <div>
		{ arrayGenerator(count).map(i => <Button key={ i }>Test</Button>) }
	</div>
};

export default El;


