import React, { Component } from 'react';

import Test from './Test';

import Test1 from './tests/ButtonFlatUO';
import Test2 from './tests/ButtonFlatO1UO';
import Test3 from './tests/ButtonFlat';
import Test4 from './tests/ButtonFlatO1';

const count = 1000;

class App extends Component {
	render() {
		return (
			<div className="App">
				<Test>
					<Test1 count={count} />
				</Test>

				<Test>
					<Test2 count={count} />
				</Test>

				<Test>
					<Test3 count={count} />
				</Test>

				<Test>
					<Test4 count={count} />
				</Test>
			</div>
		);
	}
}

export default App;



